.UserID-Wrapper {
    position: relative;

    display: inline-block;

    height: var(--user-id-size);
    padding: 0;

    font-size: 0;

    border: none;
    background: none;
}

.UserID-Wrapper_child {
    --user-id-badge-top: 90%;
}

.UserID-Account {
    position: relative;
    z-index: 0;

    display: inline-block;

    padding: 0;

    cursor: pointer;
    text-decoration: none;

    color: #000;
    border: none;
    border-radius: 100%;
    background-color: transparent;
}

.UserID-Account:focus {
    outline: none;
}
