@import '~@yandex-music-int/sol/mixins/gridLayout.scss';

@mixin collectionPageHeader {
    display: flex;
    position: sticky;
    inset-block-start: 0;

    z-index: var(--ym-z-index-header);

    align-items: center;

    background-color: var(--ym-background-color-primary-enabled-content);

    padding-block-start: var(--ym-spacer-size-l);
    padding-block-end: var(--ym-spacer-size-l);
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-xxl);

    color: var(--ym-controls-color-primary-text-enabled_variant);
    gap: var(--ym-spacer-size-xl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        gap: var(--ym-spacer-size-m);

        padding: var(--ym-spacer-size-l);
    }
}
