@import '~@yandex-music-int/sol/mixins/gridLayout.scss';
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/firefoxStyles.scss';
@import '../mixins/CollectionPageHeader.modules.scss';

.root {
    position: relative;

    padding: 0;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 0;

    height: calc(100% - var(--collection-page-header-height));
}

.header {
    @include collectionPageHeader;
}

.content {
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-s);

    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-xxl);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-l);

        @include firefoxStyles() {
            padding-inline-end: var(--ym-spacer-size-l);
        }
    }

    @include gridLayout;
}

.footer {
    padding-block-start: var(--ym-spacer-size-xxxl);
    padding-block-end: var(--ym-spacer-size-xxl);
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-s);

    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-xxl);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-block-start: var(--ym-spacer-size-l);
        padding-block-end: var(--ym-spacer-size-xxxl);
        padding-inline-start: var(--ym-spacer-size-l);

        @include firefoxStyles() {
            padding-inline-end: var(--ym-spacer-size-l);
        }
    }
}
