.root {
    display: flex;
    gap: var(--ym-spacer-size-l);

    align-items: center;
}

.userId {
    --user-id-outline-color: var(--ym-controls-color-secondary-text-selected);

    & > button {
        will-change: transition;
        transition: transform var(--ym-duration-transition);

        &:hover,
        &:focus-visible {
            transform: scale(1.05);
        }

        @media screen and (prefers-reduced-motion: reduce) {
            transition: none;
        }
    }
}

.login {
    color: var(--white);
}

/*
    Класс .dialog навешивается на внешний компонент Lego/UserID, поэтому, чтобы перезатереть
    некоторые стили, необходимо использовать именно те свойства, которые используются в UserID
*/

.dialog {
    z-index: var(--ym-z-index-overlay);

    box-shadow: none;
    /* stylelint-disable-next-line scale-unlimited/declaration-strict-value */
    background-color: unset;

    &_desktop {
        /* stylelint-disable-next-line plus-i18n/use-logical-spec */
        top: unset;
        /* stylelint-disable-next-line plus-i18n/use-logical-spec */
        right: unset;
        /* stylelint-disable-next-line plus-i18n/use-logical-spec */
        left: unset;
        inset-block-end: calc(var(--ym-spacer-size-xxxxl) + var(--ym-spacer-size-m));
        inset-inline-start: -6px;

        border: 1px solid var(--ym-controls-color-primary-outline-enabled);

        [dir='rtl'] & {
            /* stylelint-disable-next-line plus-i18n/use-logical-spec */
            left: unset;
            inset-inline-start: -6px;
        }
    }
}

.meta {
    display: flex;
    flex-direction: column;
    gap: var(--ym-spacer-size-xxxs);

    flex-grow: 1;

    min-width: 0;
    height: fit-content;
}

.plusLink {
    will-change: transition;
    transition: transform var(--ym-duration-transition);

    width: fit-content;

    &:hover,
    &:focus-visible {
        transform: scale(1.02);
    }

    @media screen and (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.userName {
    color: var(--ym-controls-color-primary-text-enabled_variant);
}
