@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-block-start: 28vh;
    padding: var(--ym-spacer-size-xxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding: var(--ym-spacer-size-l);
    }
}

.title {
    text-align: center;

    color: var(--ym-controls-color-primary-text-enabled_variant);

    // TODO: убрать дополнительный селектор после фикса https://st.yandex-team.ru/MFRONT-5960
    &.important {
        margin-block-end: var(--ym-spacer-size-xxs);
    }
}

.text {
    max-width: 430px;

    text-align: center;

    // TODO: убрать дополнительный селектор после фикса https://st.yandex-team.ru/MFRONT-5960
    &.important {
        padding-block-end: var(--ym-spacer-size-xxxxl);
    }
}

.button {
    min-width: 110px;
}
