@import '~@yandex-music-int/sol/mixins/fontStyles.scss';

.root {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: var(--ym-spacer-size-l);

    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.image {
    position: absolute;

    z-index: -1;

    will-change: opacity;
    transition: opacity ease-in-out 500ms;

    width: 100%;
    height: 100vh;
    overflow: hidden;
    object-fit: cover;

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.placeholder {
    filter: blur(100px);
}

.secondBackground {
    opacity: 0;

    &_active {
        opacity: 1;
    }
}

.loginButton {
    min-width: 120px;

    &:hover,
    &:focus-visible {
        & ~ .secondBackground {
            opacity: 1;
        }
    }
}

.offerButton {
    background: var(--ym-controls-color-primary-default-plus);

    &.offerButton_important {
        padding-block-start: var(--ym-spacer-size-l);
        padding-block-end: var(--ym-spacer-size-l);
        padding-inline-start: var(--ym-spacer-size-xxxl);
        padding-inline-end: var(--ym-spacer-size-xxxl);

        color: var(--ym-controls-color-secondary-on_default-enabled);

        @include fontStyles(
            $font-family: var(--ym-font-text),
            $font-size: var(--ym-font-size-label-l),
            $line-height: var(--ym-font-line-height-label-l),
            $font-weight: var(--ym-font-weight-medium),
            $letter-spacing: var(--ym-letter-spacing-l)
        );
    }
}

.offerButtonLink {
    margin-block-start: var(--ym-spacer-size-l);
}

.title {
    line-height: var(--ym-font-line-height-headline-xl) !important;
}

.text {
    text-align: center;

    color: var(--ym-controls-color-secondary-text-enabled_variant);
}

.offerText {
    color: var(--ym-controls-color-secondary-text-enabled);
}

.user {
    position: fixed;

    inset-block-end: var(--ym-spacer-size-xxxxl);
    inset-inline-start: var(--ym-spacer-size-xxxxl);
}
