/* stylelint-disable a11y/media-prefers-reduced-motion */
@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    display: flex;
    position: fixed;
    inset-block-start: 0;
    inset-inline-start: 0;

    z-index: 1000;

    flex-direction: column;
    align-items: center;
    justify-content: center;

    background-color: rgb(0, 0, 0);

    width: 100vw;
    height: 100vh;

    &_hidden {
        animation: fade 0.1s forwards;

        @media screen and (prefers-reduced-motion: reduce) {
            animation: fade 0s forwards;
        }
    }
}

.video {
    opacity: 0;

    width: 65%;

    &_visible {
        opacity: 1;
    }

    @include aspectRatio(1, 1);
}

@keyframes fade {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}
