.UserID-Badge {
    position: relative;

    display: inline-flex;

    vertical-align: middle;
}

.UserID-Badge-Dot {
    position: absolute;
    top: 8%;
    left: 92%;

    display: inline-block;

    box-sizing: border-box;
    min-width: 16px;
    padding: 1px 4px;

    font-family: 'YS Text', Arial, Helvetica, 'Arial Unicode MS', sans-serif;
    font-size: 11px;
    font-weight: 500;
    line-height: 14px;
    user-select: none;
    text-align: center;
    white-space: nowrap;

    color: #fff;
    border-radius: 8px;
    background-color: #fc4714;

    transform: translate(-50%, -50%);
}

.UserID-Badge-Dot:empty {
    display: none;
}
