@import './breakpoints.scss';

@mixin gridLayout() {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: var(--ym-spacer-size-m);

    @include mediaBreakpointUp(360) {
        grid-template-columns: repeat(2, 1fr);
    }

    @include mediaBreakpointUp(500) {
        grid-template-columns: repeat(3, 1fr);
    }

    @include mediaBreakpointUp($ym-breakpoint-xxs) {
        grid-template-columns: repeat(3, 1fr);
    }

    @include mediaBreakpointUp(890) {
        grid-template-columns: repeat(4, 1fr);
    }

    @include mediaBreakpointUp($ym-breakpoint-xs) {
        gap: calc(2 * var(--ym-spacer-size-m));
    }

    @include mediaBreakpointUp(1200) {
        grid-template-columns: repeat(5, 1fr);
    }

    @include mediaBreakpointUp(1550) {
        grid-template-columns: repeat(6, 1fr);
    }

    @include mediaBreakpointUp($ym-breakpoint-xl) {
        grid-template-columns: repeat(7, 1fr);
    }

    @include mediaBreakpointUp($ym-breakpoint-xxl) {
        grid-template-columns: repeat(8, 1fr);
    }

    @include mediaBreakpointUp($ym-breakpoint-xxxl) {
        grid-template-columns: repeat(9, 1fr);
    }
}
